/*--------------------------------------------------------------
# Layout
--------------------------------------------------------------*/
body {
  overflow-x: hidden; }

:root {
  --margin-y-1: 2rem;
  --margin-y-2: 3.5rem;
  --padding-x:15px;
  --padding-x-largeur-reduite:var(--padding-x);
  --padding-x-pleine-largeur:0; }
  @media (min-width: 500px) {
    :root {
      --padding-x: 4vw; } }
  @media (min-width: 768px) {
    :root {
      --padding-x-largeur-reduite: calc(50vw - 60ch/2); } }
  @media (min-width: 960px) {
    :root {
      --margin-y-1: 4.5rem;
      --margin-y-2: 7.5rem; } }
  @media (min-width: 1920px) {
    :root {
      --padding-x: calc(50vw - 1613px/2);
      --padding-x-pleine-largeur: calc(50vw - 1920px/2); } }

.site-main .fil-ariane, .site-main h1 {
  padding-left: var(--padding-x);
  padding-right: var(--padding-x); }

body.largeur-normale .entry-content {
  padding: 0 var(--padding-x); }

body.largeur-reduite .entry-content {
  padding: 0 var(--padding-x-largeur-reduite); }

body.pleine-largeur .entry-content {
  padding: 0 var(--padding-x-pleine-largeur); }

.alignfull, .bleed-container {
  width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
  padding-left: 0;
  padding-right: 0; }

.container {
  padding: 0 var(--padding-x); }

/* Hauteur mini main*/
.site {
  overflow-x: hidden;
  position: relative; }

.site-main {
  min-height: 100vh; }

/* Grid */
.flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; }

.show-for-md,
.show-for-lg {
  display: none; }

@media (min-width: 768px) {
  .show-for-md {
    display: initial; }
  .hide-for-md {
    display: none; } }

@media (min-width: 960px) {
  .show-for-lg {
    display: initial; }
  .hide-for-lg {
    display: none; } }

/* Overlay */
.site-main {
  position: relative; }

.overlay {
  background-color: #2D2F38;
  display: none;
  opacity: 0.8;
  transition: opacity var(--transition); }
  .overlay.toggled {
    display: block;
    height: 100%;
    width: 100%;
    z-index: 5;
    position: absolute;
    top: 0;
    left: 0; }

/* Echelle z-index 
Elements qui ont juste besoin d'être au-dessus de leur voisin : 1
Overlay : 5
Volet questionnaire mobile : 6
Sticky header : 10
Menu mobile : 10
Sous-menu desktop : 10;

*/
:root {
  --padding-lien:7px 12px; }

html {
  font-size: 62.5%;
  /* 1rem = 10px */
  --taille-texte:1.8rem; }
  @media (min-width: 1400px) {
    html {
      --taille-texte:2.1rem; } }

body,
button,
input,
select,
optgroup,
textarea {
  color: #303030;
  color: var(--texte);
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.8rem;
  font-size: var(--taille-texte);
  font-weight: 300;
  line-height: 1.4; }

::selection {
  background: var(--bleu);
  color: var(--blanc); }

h1, .h1 {
  color: var(--noir);
  font-size: calc(55 * var(--taille-texte) / 21);
  font-weight: 400;
  margin: 1.5em 0 1em;
  padding: 0;
  text-align: center;
  text-transform: uppercase; }

h2,
h3,
h4,
.h2, .h3, .h4 {
  color: var(--bleu);
  clear: both;
  font-weight: 700;
  margin: 0 0 0.5em; }

h2, .h2 {
  font-size: calc(28 * var(--taille-texte) / 21); }

p + h2, p + .h2 {
  margin-top: 2em; }

h3, .h3 {
  font-size: calc(26 * var(--taille-texte) / 21); }

h4, .h4, h5, .h5, h6, .h6 {
  font-size: calc(24 * var(--taille-texte) / 21); }

p {
  margin-bottom: 1em;
  margin-top: 0; }

dfn,
cite,
em,
i {
  font-style: italic; }

blockquote {
  border-left: solid 5px var(--bleu-fonce);
  font-size: 1.2em;
  margin: 0 0 1em;
  padding: 5px 27px; }
  blockquote cite {
    font-weight: 500; }

mark,
ins {
  background: var(--fond);
  text-decoration: none; }

small, .has-small-font-size {
  font-size: 80%; }

.has-regular-font-size {
  font-size: 100%; }

big, .has-big-font-size {
  font-size: 125%; }

.has-huge-font-size {
  font-size: 200%; }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */ }

.screen-reader-text:focus {
  background-color: var(--fond);
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  clip-path: none;
  display: block;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */ }

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
  outline: 0; }

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0s !important;
    transition-duration: 0s !important; } }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5em; }

ul:not([class]),
ol:not([class]) {
  margin: 0 0 1.5em 0;
  padding-left: 1em; }

ul:not([class]) {
  list-style: none;
  padding-left: 1em; }
  ul:not([class]) > li::before {
    content: '•';
    color: var(--bleu);
    display: inline-block;
    width: 1em;
    margin-left: -1em; }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5em; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5em 1.5em; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

figure {
  margin: 1em 0;
  /* Extra wide images within figure tags don't overflow the content area. */ }
  figure figcaption {
    text-align: center; }

table {
  margin: 0 0 1.5em;
  width: 100%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"],
a.button,
a.wp-block-button__link {
  border: none;
  border-radius: 1rem;
  cursor: pointer;
  display: inline-block;
  font-size: 1em;
  font-weight: 400;
  letter-spacing: 1.05px;
  line-height: 1.1;
  min-height: 4.4rem;
  min-width: 21rem;
  padding: 1rem 2rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all var(--transition);
  vertical-align: middle;
  background: var(--bleu-bouton);
  color: var(--blanc); }
  button:hover, button:focus, button:active,
  input[type="button"]:hover,
  input[type="button"]:focus,
  input[type="button"]:active,
  input[type="reset"]:hover,
  input[type="reset"]:focus,
  input[type="reset"]:active,
  input[type="submit"]:hover,
  input[type="submit"]:focus,
  input[type="submit"]:active,
  a.button:hover,
  a.button:focus,
  a.button:active,
  a.wp-block-button__link:hover,
  a.wp-block-button__link:focus,
  a.wp-block-button__link:active {
    background: var(--bleu-fonce);
    outline: none;
    text-decoration: none; }
  button:focus-visible,
  input[type="button"]:focus-visible,
  input[type="reset"]:focus-visible,
  input[type="submit"]:focus-visible,
  a.button:focus-visible,
  a.wp-block-button__link:focus-visible {
    outline: solid 1px var(--bleu-fonce); }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
  color: var(--texte);
  border: 1px solid var(--gris);
  border-radius: 1rem;
  outline-color: var(--bleu);
  min-height: 4.4rem;
  padding: 3px; }
  input[type="text"]:focus,
  input[type="email"]:focus,
  input[type="url"]:focus,
  input[type="password"]:focus,
  input[type="search"]:focus,
  input[type="number"]:focus,
  input[type="tel"]:focus,
  input[type="range"]:focus,
  input[type="date"]:focus,
  input[type="month"]:focus,
  input[type="week"]:focus,
  input[type="time"]:focus,
  input[type="datetime"]:focus,
  input[type="datetime-local"]:focus,
  input[type="color"]:focus,
  textarea:focus {
    color: var(--bleu); }

select {
  border: 1px solid var(--gris); }

textarea {
  width: 100%; }

/*--------------------------------------------------------------
## Search form
--------------------------------------------------------------*/
.search-form {
  align-items: stretch;
  justify-content: flex-start; }

.search-form .search-field {
  height: 40px;
  line-height: 40px;
  margin-bottom: 0; }

.search-form .search-submit {
  margin-bottom: 0;
  margin-left: 14px;
  padding: 0;
  height: 40px;
  line-height: 40px;
  text-align: center;
  width: 49px; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
  margin-bottom: 1.5em;
  max-width: 100%; }

.wp-caption img[class*="wp-image-"] {
  display: block;
  margin-left: auto;
  margin-right: auto; }

.wp-caption .wp-caption-text {
  margin: 0.8075em 0; }

.wp-caption-text {
  text-align: center; }

.wp-block-button__link {
  background-color: var(--bleu-bouton);
  text-transform: uppercase; }

.block-editor-writing-flow > * {
  font-size: 50%; }

.wp-block-columns.antennes-regionales {
  align-items: center; }
  .wp-block-columns.antennes-regionales ol {
    color: var(--bleu);
    font-weight: 700; }
    .wp-block-columns.antennes-regionales ol li {
      margin-bottom: 1em; }

.decor-traits {
  position: relative; }
  .decor-traits::before {
    background-image: url("icons/decor-droit.svg");
    background-position: bottom right;
    background-repeat: no-repeat;
    background-size: contain;
    content: '';
    position: absolute;
    height: 600px;
    right: -300px;
    width: 545px;
    top: -200px; }
    @media (min-width: 768px) {
      .decor-traits::before {
        top: -120%;
        height: 872px;
        right: -90%;
        width: 823px; } }

.wp-block-image figcaption {
  font-style: italic; }

.wp-block-image.alignwide {
  width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
  padding: 0 var(--padding-x-grille); }
  .wp-block-image.alignwide figcaption {
    text-align: right; }

/*Galeries créées avec Meow Gallery*/
.mgl-masonry-container, .mgl-tiles-container, .mgl-justified-container, .mgl-square-container,
.mgl-cascade-container {
  width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
  padding: 0 var(--padding-x-grille);
  margin-bottom: var(--margin-y); }
